import './index.less'

import * as React from 'react'
import { Route, Switch } from 'react-router-dom'
import { inject, observer } from 'mobx-react'

import Issue from './Issue'
import Issues from './Issues'
import { IssueStore } from '../../store/issueStore'
import { hot } from 'react-hot-loader';

export interface IHomeProps {
  issueStore: IssueStore
}

export interface IHomeState { }

@inject('issueStore')
@observer
class Home extends React.Component<IHomeProps, IHomeState> {

  state = {
    owner: '',
    repo: '',
  }

  handleInputChange = (e: any) => {
    const target = e.target
    const name = target.name;
    this.setState({
      [name]: target.value
    });
  }

  handleSubmit = () => {
    if (this.state.owner && this.state.repo) {
      this.props.issueStore.changeRepo(this.state.owner, this.state.repo)
      this.props.issueStore.getIssues({ page: 1 })
    }
  }

  render() {
    return (
      <div className='home'>
        <header>
          <div className='form'>
            <input type="text" name='owner' defaultValue={this.state.owner} onChange={this.handleInputChange} />
            <input type="text" name='repo' defaultValue={this.state.repo} onChange={this.handleInputChange} />
            <button onClick={this.handleSubmit}>submit</button>
          </div>
        </header>
        <Switch>
          <Route path="/issues" component={Issues} />
          <Route path="/issue/:id" component={Issue} />
        </Switch>
      </div>
    )
  }
}
export default hot(module)(Home)